import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

/*
  Generated class for the AppServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

//全局信息
@Injectable()
export class AppGlobal {

  static cache: any = {
    slides: "_dress_slides",
    categories: "_dress_categories",
    products: "_dress_products"
  }
  static domain = "http://218.75.181.68:8306";
  //static domain="http://10.100.33.218:8306";//测试环境
  //static domain="http://10.100.33.27:8306";//测试环境
  // static domain = "http://192.168.0.104:8306";//测试环境

  static API = {
    login: '/api/sys/applogin', //登录接口
    appAutoCode: '/api/sys/appAutoCode',  //启动时登录验证
    logout: '/api/sys/logout',  //退出登录
    getDataByStandard: '/api/home/getDataByStandard',// 获取汨罗首页省控
    getDataByState: '/api/home/getDataByState',// 获取汨罗首页国控
    getSiteTree: '/api/comment/getSiteTree',// 站点树
    getListAlert: '/api/alert/getListAlert',//报警
    getAirData: '/api/statistics/getAirData',//统计空气
    getPolluteWaterData: '/api/statistics/getPolluteWaterData',//统计污水处理厂
    getPolluteData: '/api/statistics/getPolluteData  ',//统计污染源
    getWaterData: '/api/statistics/getWaterData',//统计水质自动站
    getTaskList: '/api/task/getTaskList',//待办、历史记录
    getUserList: '/api/comment/getUserList',//获取所有用户
    getHandler: '/api/task/getHandler',//通过角色获取用户
    saveTask: '/api/task/saveTask',//添加任务
    updateTaskById: '/api/task/updateTaskById',//编辑任务
    getMapData: '/api/map/getMapData',//地图
    querySiteInfo: '/api/map/querySiteInfo',//地图详情
    getSiteList: '/api/comment/getSiteList',//站点列表
    clearVideo: 'https://open.ys7.com/api/lapp/device/ptz/stop',//结束操作
    changeVideoDirection: 'https://open.ys7.com/api/lapp/device/ptz/start',//开始操作
  };

  static RETURNCODE: any = {
    succeed: 200//成功
  }

}

@Injectable()
export class AppServiceProvider {
  private static instance: AppServiceProvider = new AppServiceProvider();
  //当前用户信息
  public userInfo: any = {
    username: "",
    password: "",
    userId: "",
    code: "",
    role: ""
  };
  public siteList: any;
  public factorInfo: any;
  public spleTeam: string = "";
  public teamMember: string = "";


  /**
   * 获取应用单例
   * 
   * @static  constructor() {
    if (AppServiceProvider.instance) {
      throw new Error("错误: 请使用AppServiceProvider.getInstance() 代替使用new.");
    }
    AppServiceProvider.instance = this;
  }
   * @returns {AppServiceProvider}
   */
  public static getInstance(): AppServiceProvider {
    return AppServiceProvider.instance;
  }

}


