import { AppServiceProvider } from './../providers/app-service/app-service';
import { NgModule, ErrorHandler, NgZone } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler, Platform, LoadingController, Events } from 'ionic-angular';
import { MyApp } from './app.component';
import { AppUpdate } from '@ionic-native/app-update';
import { Geolocation } from '@ionic-native/geolocation';



import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Camera } from '@ionic-native/camera';
import { NetworkServiceProvider } from '../providers/network-service/network-service';
import { StorageServiceProvider } from '../providers/storage-service/storage-service';
import { WebNetworkServiceProvider } from '../providers/web-network-service/web-network-service';
import { HTTP } from '@ionic-native/http';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { NativeStorage } from '@ionic-native/native-storage';
import { JPush } from '@jiguang-ionic/jpush';
import { Device } from '@ionic-native/device';

import { HammerGestureConfig, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
// import { VgCoreModule } from 'videogular2/core';
// import { VgControlsModule } from 'videogular2/controls';
// import { VgOverlayPlayModule } from 'videogular2/overlay-play';
// import { VgBufferingModule } from 'videogular2/buffering';

export class MyHammerConfig extends HammerGestureConfig {
  overrides = <any>{
    // override hammerjs default configuration
    'swipe': {
      velocity: 0.2,
      // threshold: 15,
      // direction: 30
    },
 
    'press':{
      time: 0.1,
    }
  }
}

export function netFactory(platform:Platform, loadingCtrl:LoadingController, http:HTTP, httpClient:HttpClient, zone:NgZone, events?: Events) {
  if (platform.is("mobileweb") /*||platform.is("mobile")*/) {
    return new WebNetworkServiceProvider(httpClient,loadingCtrl,events);
  }else if(platform.is("mobile")){
    return new NetworkServiceProvider(loadingCtrl, http, httpClient, zone, events);
  }else{
    return new WebNetworkServiceProvider(httpClient, loadingCtrl, events);
  }
}
@NgModule({
  declarations: [
    MyApp,

  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {
      tabsHideOnSubPages: 'true',         //隐藏全部子页面tabs
      backButtonText: '',
      iconMode: 'ios',       //安卓icon强制使用ios的icon以及样式
      mode: 'ios',          //样式强制使用ios样式
      menuType: 'overlay', //菜单栏类型
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,

  ],
  providers: [
    StatusBar,
    SplashScreen,
    HTTP,
    MyApp,
    JPush,
    Device,

    { provide: ErrorHandler, useClass: IonicErrorHandler },
    {
      provide: NetworkServiceProvider, useFactory: netFactory,
      deps: [Platform, LoadingController, HTTP, HttpClient, NgZone, Events]
    },
    {
      provide: HAMMER_GESTURE_CONFIG,
      useClass: MyHammerConfig
    },
    AppServiceProvider,
    StorageServiceProvider,
    WebNetworkServiceProvider,
    NativeStorage,
    AppUpdate,
    Geolocation,
    Camera,
  ]
})
export class AppModule { }



