import { HTTP } from '@ionic-native/http';
import { HttpClient } from '@angular/common/http';
import { AppGlobal } from './../app-service/app-service';
import { Injectable, NgZone } from '@angular/core';
import { LoadingController, Events } from 'ionic-angular';
/*
  Generated class for the TyNetworkServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NetworkServiceProvider {
  constructor(public loadingCtrl: LoadingController, public http?:HTTP, public httpClient?: HttpClient, public zone?:NgZone, public events?: Events) {

  }
  encode(params) {
    var str = '';
    if (params) {
        for (var key in params) {
            if (params.hasOwnProperty(key)) {
                var value = params[key];
                str += encodeURIComponent(key) + '=' + encodeURIComponent(value) + '&';
            }
        }
        str = '?' + str.substring(0, str.length - 1);
    }
    return str;
  }

  httpPost(api, params, success,failed, loader: boolean = true) {

    //兼容部分接口模拟数据//
    // if("common|queryNews" == params.ACTION_NAME){
    //   this.webGet(url, params, success,failed, loader);
    //   return;
    // }
    
    console.log("app_post:"+AppGlobal.domain+api+"-->"+JSON.stringify(params));
    let loading = this.loadingCtrl.create();

    if (loader) {
        loading.present();
    }
    this.http.post(AppGlobal.domain + api, params ,{}).then(res => {
      if (loader) {
        loading.dismiss();
      }
      if(res.data){
        let data = JSON.parse(res.data);
        switch(data.retCode){
          case 200:
            success(data.data);
            break;
          case 150:
            this.events.publish("tokenError150");
            break;
          default:
            failed(data.message);
            break;
        }
      }
    }).catch( error => {
      if (loader) {
        loading.dismiss();
      }
      failed(error);
    });
  }
  httpGet(url, params, success,failed, loader: boolean = true) {
    console.log("app_Get:"+url+"-->"+JSON.stringify(params));
    let loading = this.loadingCtrl.create();
    if (loader) {
        loading.present();
    }
    this.http.get( AppGlobal.domain + url + this.encode(params), null, {}).then( res => {
      if (loader) {
        loading.dismiss();
      }
      if(res.data){
        let data = JSON.parse(res.data);
        switch(data.retCode){
          case 200:
            success(data.data);
            break;
          case 150:
            this.events.publish("tokenError150");
            break;
          default:
            failed(data.message);
            break;
        }
      }
    }).catch(error => {
      if (loader) {
        loading.dismiss();
      }
      failed(error);
    });
  }
  webPost(api, params, success,failed, loader: boolean = true) {

    //兼容部分接口模拟数据//
    // if("common|queryNews" == params.ACTION_NAME){
    //   this.webGet(url, params, success,failed, loader);
    //   return;
    // }
    
    console.log("app_post:"+api+"-->"+JSON.stringify(params));
    let loading = this.loadingCtrl.create();

    if (loader) {
        loading.present();
    }
    this.http.post(api, params ,{}).then(res => {
      if (loader) {
        loading.dismiss();
      }
      if(res.data){
        let data = JSON.parse(res.data);
        switch(data.retCode){
          case 200:
            success(data.data);
            break;
          case 150:
            this.events.publish("tokenError150");
            break;
          default:
            failed(data.message);
            break;
        }
      }
    }).catch( error => {
      if (loader) {
        loading.dismiss();
      }
      failed(error);
    });
  }
  ///////////////////////////////////////////兼容模拟数据/////////////////////////////////////////////////////////////
  
  webGet(url, params, success,failed, loader: boolean = true) {
    let loading = this.loadingCtrl.create();
    if (loader) {
        loading.present();
    }
    let actioname = params.ACTION_NAME.replace("|","");
    let murl ="assets/data/"+actioname+'.json';
    this.httpClient.get(murl).subscribe( res => {
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        
        let m=  res.toString();
        var reg = /("([^\\\"]*(\\.)?)*")|('([^\\\']*(\\.)?)*')|(\/{2,}.*?(\r|\n))|(\/\*(\n|.)*?\*\/)/g;
        let str = m.replace(reg,function(word) { // 去除注释后的文本  
          return /^\/{2,}/.test(word) || /^\/\*/.test(word) ? "" : word;  
      });
        success(str);
      }, 1000);
    }, error => {
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log(error);
        failed(error.message);
      }, 1000);
    });        
  }
}



