import { Platform, ToastController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { NativeStorage } from '@ionic-native/native-storage';
/*
  Generated class for the StorageServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StorageServiceProvider {
  webFlag: boolean;
  constructor(public storage: NativeStorage, public platform: Platform, public toastController: ToastController) {
    if (platform.is("mobileweb") /*||platform.is("mobile")*/) {
      this.webFlag = true;
    } else if (platform.is("mobile")) {
      this.webFlag = false;
    } else {
      this.webFlag = true;
    }
  }
  clrar() {
    if (this.webFlag) {
      window.localStorage.clear();
    } else {
      this.storage.clear();
    }
  }
  getItem(key, success, faild?) {
    if (this.webFlag) {
      let res = window.localStorage.getItem(key);
      console.log(res);
      if (res) {
        success(JSON.parse(res));
      } else {
        console.log("###########");

        success(res);
      }
    } else {
      this.storage.getItem(key).then(res => {
        if (res) {
          success(JSON.parse(res));
        } else {
          success(res);
        }
      }, error => {
        faild();
        console.log("storage:" + error);
      });
    }
  }
  keys(success) {
    if (this.webFlag) {
      let keys: Array<string> = [];
      for (let i = 0; i < window.localStorage.length; i++) {
        var key = window.localStorage.key(i);
        keys.push(key);
      }
      success(keys);
    } else {
      this.storage.keys().then(res => {
        success(res);
      }, error => { });
    }
  }
  remove(key) {
    if (this.webFlag) {
      window.localStorage.removeItem(key);
    } else {
      this.storage.remove(key);
    }
  }
  setItem(key, value) {
    value = JSON.stringify(value);
    if (this.webFlag) {
      window.localStorage.setItem(key, value);
    } else {
      this.storage.setItem(key, value);
    }
  }
  //toast时间控制
  popToastView(message: string, duration: number) {
    this.toastController.create({
      message: message,
      position: 'middle',
      duration: duration,
      showCloseButton: true,
      closeButtonText: "关闭"
    }).present();
  }
}



