import { HttpParams } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { LoadingController, Events } from 'ionic-angular';
import { AppGlobal } from '../app-service/app-service';
/*
  Generated class for the WebNetworkServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class WebNetworkServiceProvider {
  constructor(public http:HttpClient, public loadingCtrl: LoadingController, public events?: Events) {
      
  }
  
  encode(params) {
    var str = '';
    if (params) {
        for (var key in params) {
            if (params.hasOwnProperty(key)) {
                var value = params[key];
                str += encodeURIComponent(key) + '=' + encodeURIComponent(value) + '&';
            }
        }
        str = '?' + str.substring(0, str.length - 1);
    }
    return str;
  }

  httpPost(api, params, success,failed, loader: boolean = true) {
    //this.httpGet(url, params, success,failed, loader);
    let loading = this.loadingCtrl.create();
    if (loader) {
        loading.present();
    }
    let httpParams = new HttpParams();
    for (const key in params) {
      httpParams = httpParams.set(key, params[key]);
    }
    console.log("web_request url:"+AppGlobal.domain+api);
    console.log("web_request data:"+JSON.stringify(params));
    this.http.post(AppGlobal.domain + api,httpParams).subscribe( res => {
      let data = JSON.parse(JSON.stringify(res));
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log(data);
        switch(data.retCode){
          case 200:
            success(data.data);
            break;
          case 150:
            this.events.publish("tokenError150");
            break;
          default:
            failed(data.message);
            break;
        }
      }, 500);
    }, error => {
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log(error);
        failed(error.message);
      }, 500);
    });
  }
  httpGet(api, params, success,failed, loader: boolean = true) {
    let loading = this.loadingCtrl.create();
    if (loader) {
        loading.present();
    }
    this.http.get(AppGlobal.domain + api + this.encode(params)).subscribe( res => {
      let data = JSON.parse(JSON.stringify(res));
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log("web_respone:" + JSON.stringify(data));
        switch(data.retCode){
          case 200:
            success(data.data);
            break;
          case 150:
            this.events.publish("tokenError150");
            break;
          default:
            failed(data.message);
            break;
        }
      }, 500);
    }, error => {
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log(error);
        failed(error.message);
      }, 500);
    });
  }

  webPost(api, params, success,failed, loader: boolean = true) {
    //this.httpGet(url, params, success,failed, loader);
    let loading = this.loadingCtrl.create();
    if (loader) {
        loading.present();
    }
    let httpParams = new HttpParams();
    for (const key in params) {
      httpParams = httpParams.set(key, params[key]);
    }
    console.log("web_request url:"+api);
    console.log("web_request data:"+JSON.stringify(params));
    this.http.post( api,httpParams).subscribe( res => {
      let data = JSON.parse(JSON.stringify(res));
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log(data);
        switch(data.retCode){
          case 200:
            success(data.data);
            break;
          case 150:
            this.events.publish("tokenError150");
            break;
          default:
            failed(data.message);
            break;
        }
      }, 500);
    }, error => {
      setTimeout(() => {
        if (loader) {
          loading.dismiss();
        }
        console.log(error);
        failed(error.message);
      }, 500);
    });
  }
}



