import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, Events, ToastController, IonicApp, Keyboard, App, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StorageServiceProvider } from '../providers/storage-service/storage-service';
import { NetworkServiceProvider } from '../providers/network-service/network-service';
import { AppServiceProvider, AppGlobal } from '../providers/app-service/app-service';
import { AppUpdate } from '@ionic-native/app-update';
import { Device } from '@ionic-native/device';
import { JPush } from '@jiguang-ionic/jpush';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild("myNav") nav: Nav;
  backButtonPressed: boolean = false;//用于判断返回键是否触发
  rootPage: any = 'TabsPage';
  devicePlatform: string;//平台类型
  rootPages: Array<string> = [ "LoginPage","HomePage","MapPage","WamingPage","StatisticsPage","ContactPage", "TabsPage"];
  constructor(public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public storage: StorageServiceProvider,
    public http: NetworkServiceProvider,
    public events: Events,
    public toast: ToastController,
    public ionicApp: IonicApp,
    public keyboard: Keyboard,
    public app: App,
    public appUpdate: AppUpdate,
    //public navCtrl:NavController,
    public alerCtrl: AlertController,
    device: Device,
    jpush: JPush, ) {
    this.eventsregisterTokenErrEvent();
    this.veriftyUser();

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      //this.devicePlatform = device.platform;

      if (this.platform.is("android")) {
        this.statusBar.backgroundColorByHexString("#17D4CD");//状态栏header背景色
        this.registerBackButtonAction();
        this.update();
      }
      this.platform.registerBackButtonAction(() => {//当用户按下后退按钮时执行的代码
        app.navPop();
      });
      splashScreen.hide();//隐藏启动页面
      // jpush.init();
      // jpush.setDebugMode(true);
      // this.jPushListener();
    });
  }

  update() {
    const updateUrl = AppGlobal.domain+"/mlAPP/version.xml";
    this.appUpdate.checkAppUpdate(updateUrl).then((res => {
      // if (res.cole == 202 || res.code == '202') {
      //   this.toast.create({
      //     message: '该当前已是最新版本',
      //     duration: 2000,
      //     position: 'bottom',
      //     cssClass: 'text-align: center'
      //   }).present();
      // }
    }), error => {
      console.log(error);
    })
  }
  jPushListener() {
    document.addEventListener("jpush.receiveNotification", (event: any) => {
      var content;
      if (this.devicePlatform == "Android") {
        content = event.alert;
        console.log(content);
      } else {
        content = event.aps.alert;
        console.log(content);
      }
      //alert("Receive notification: " + JSON.stringify(event));
    },
      false
    );

    document.addEventListener("jpush.openNotification", (event: any) => {
      var content;
      if (this.devicePlatform == "Android") {
        //content = event.alert;
        content = event.extras;
        console.log(content)
        //this.events.publish("WarningPage", event.extras,Date.now());
        //alert(JSON.stringify(content));
      } else {
        // iOS
        if (event.aps == undefined) {
          // 本地通知
          content = event.content;
          console.log(content);
        } else {
          // APNS
          content = event.aps.alert;
          console.log(content);
        }
      }
      // alert(content.police);
      var data = JSON.parse(content.police);
      // alert(data.siteName);
      let alerts = this.alerCtrl.create({
        title: data.siteName,
        message: "<div>站点名称: " + data.siteName + "</div><div>报警时间: " + this.convertToDate(data.exceedsBidDate) + "</div><div>报警因子: " + data.pollutant + "</div><div>监测值: " + data.value + "</div><div>指标值: " + data.indexValue + "</div>",
        buttons: ["确定"]
      });
      alerts.present();
    }, false); 1

    document.addEventListener("jpush.receiveLocalNotification", (event: any) => {
      // iOS(*,9) Only , iOS(10,*) 将在 jpush.openNotification 和 jpush.receiveNotification 中触发。
      let content;
      if (this.devicePlatform == "Android") {
      } else {
        content = event.content;
        console.log(content);
      }
      //alert("receive local notification: " + JSON.stringify(event));
    }, false);
  }
  veriftyUser() {
    let userInfo = AppServiceProvider.getInstance().userInfo;
    if (!userInfo || userInfo.code == null || userInfo.code == '') {
      this.storage.getItem("userInfo", data => {
        if (data) {
          AppServiceProvider.getInstance().userInfo = data;
          // console.error("！！！！！！！！！")
          // console.error(data);
          // console.error(AppServiceProvider.getInstance().userInfo);
          let param: any = {};
          param.userId = AppServiceProvider.getInstance().userInfo.userId;
          param.code = AppServiceProvider.getInstance().userInfo.code;
          this.http.httpGet(AppGlobal.API.appAutoCode, param, data => {//启动时登录验证
          //alert(1)
    
          }, error => {
           // alert(2)
          }, false);
        } else {
          //alert(3)
          setTimeout(() => {
            this.nav.setRoot("LoginPage");
          }, 10)
        }
      }, () => {
        //alert(4)
        setTimeout(() => {
          this.nav.setRoot("LoginPage");
        }, 10)
      });
    }
  }
  /**
     * 时间戳转string类型日期
     * 2019年2月27日20点53分
     */
  convertToDate(nows) {
    const now = new Date(nows);
    const year = now.getFullYear();
    const month = now.getMonth() + 1 < 10 ? "0" + (now.getMonth() + 1) : now.getMonth() + 1;
    const date = now.getDate() < 10 ? "0" + now.getDate() : now.getDate();
    const hour = now.getHours() < 10 ? "0" + now.getHours() : now.getHours();
    const minute = now.getMinutes() < 10 ? "0" + now.getMinutes() : now.getMinutes();
    const second = now.getSeconds() < 10 ? "0" + now.getSeconds() : now.getSeconds();


    return year + '-' + month + '-' + date + ' ' + hour + ':' + minute + ':' + second;
  }
  registerBackButtonAction() {
    /**
     * 注意：
     *    1.必须在tabs.html上加  #tabs
     *    2.tabs.ts里面必须加   @ViewChild("tabs") tabs:Tabs;
     *  */
    this.platform.registerBackButtonAction(() => {
      console.log("this.keyboard.isOpen():" + this.keyboard.isOpen());
      if (this.keyboard.isOpen()) {//	是否打开或关闭菜单。默认：切换菜单。
        //按下返回键时，先关闭键盘
        this.keyboard.close();
        return;
      };

      //如果想点击返回按钮隐藏toast或loading或Overlay就把下面加上
      // this.ionicApp._toastPortal.gaetActive() || this.ionicApp._loadingPortal.getActive() || this.ionicApp._overlayPortal.getActive()
      //不写this.ionicApp._toastPortal.getActive()是因为连按2次退出
      let activePortal = this.ionicApp._modalPortal.getActive() || this.ionicApp._overlayPortal.getActive();//然后如果有其他model，页面，关闭掉，
      let loadingPortal = this.ionicApp._loadingPortal.getActive();//隐藏加载动画。实现要点：通过 this.ionicApp._loadingPortal 获取到代表 Loading 的 OverlayPortal，然后获取当前被激活的 ViewController

      if (activePortal) {
        //其他的关闭
        activePortal.dismiss().catch(() => {
        });
        activePortal.onDidDismiss(() => {
        });
        return;
      }
      if (loadingPortal) {
        //loading的话，返回键无效
        return;
      }

      let activeVC = this.nav.getActive();
      let page = activeVC.instance;
      if (page.tabs) {
        let activeNav = page.tabs.getSelected();
        if (activeNav.canGoBack()) {
          return activeNav.pop();
        } else {
          return this.showExit();
        }
      }

      console.log("events go on ");
      console.log(this.nav.getActive());
      if (this.rootPages.indexOf(this.nav.getActive().id) >= 0) {
        return this.showExit();
      }
    }, 1);
  }
  showExit() {
    if (this.backButtonPressed) { //当触发标志为true时，即1秒内双击返回按键则退出APP
      this.platform.exitApp();
    } else {
      this.toast.create({
        message: '再按一次退出应用',
        duration: 1000,
        position: 'bottom',
        cssClass: 'text-align: center'
      }).present();
      this.backButtonPressed = true;
      setTimeout(() => this.backButtonPressed = false, 1000);//1秒内没有再次点击返回则将触发标志标记为false
    }
  }
  eventsregisterTokenErrEvent() {
    this.events.subscribe('tokenError150', () => {//调用Eevents事件
      this.toast.create({
        message: '该帐号已在其他设备登录，请重新登录',
        duration: 2000,
        position: 'bottom',
        cssClass: 'text-align: center'
      }).present();
      console.log("进来了");
      setTimeout(() => {
        this.nav.setRoot("LoginPage");
      }, 10)
      // this.nav.setRoot("LoginPage");
    });
  }
}



